import React from 'react'
import { Map, GeoJSON, TileLayer, Circle } from 'react-leaflet'
import './App.css'

export default () => {
  return (
    <Map center={[50.707114, -3.071811]} zoom={12}>
      <TileLayer
        url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
      />
      <GeoJSON data={getGeoJSON()} />
      <Circle
        id="bob"
        center={[50.707114, -3.071811]}
        className="circle_style"
        fillColor="red"
        radius="10"
      />
    </Map>
  )
}

